import React from 'react';
import ReactDOM from 'react-dom';
import CurrentDay from './currentDay.js';
import NextDays from './nextDays.js'
import './index.css';

class App extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            data: null,
            lon: null,
            lat: null,
            city: null,
        }
    }

    success = (position) => {
        this.setState({lon: position.coords.longitude, lat: position.coords.latitude})
        const weatherKey = 'a913b85241698a00b1014abe62a5ca0e';
        const locationKey = '74b2d16131a57f25ad6a62156047b305';
        fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.state.lat}&lon=${this.state.lon}&exclude=hourly,minutely&appid=${weatherKey}&units=metric`)
        .then((response) => {return response.json()})
        .then((data) => {
            this.setState({data: data})
        })

        fetch(`https://api.positionstack.com/v1/reverse?access_key=${locationKey}&query=${this.state.lat},${this.state.lon}&limit=1`)
        .then((response) => {return response.json()})
        .then((data) => {
            this.setState({city: data.data[0].locality})
        })
    }

    fail = (error) => {
        this.setState({lon: 26.251617, lat: 50.619900})
        const weatherKey = 'a913b85241698a00b1014abe62a5ca0e';
        const locationKey = '74b2d16131a57f25ad6a62156047b305';
        fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.state.lat}&lon=${this.state.lon}&exclude=hourly,minutely&appid=${weatherKey}&units=metric`)
        .then((response) => {return response.json()})
        .then((data) => {
            this.setState({data: data})
        })

        fetch(`https://api.positionstack.com/v1/reverse?access_key=${locationKey}&query=${this.state.lat},${this.state.lon}&limit=1`)
        .then((response) => {return response.json()})
        .then((data) => {
            this.setState({city: data.data[0].locality})
        })
            alert('Currently we can not get your geolocation due to: ' + error.message)
    }

    async findCity (event) {
        event.preventDefault();
        const weatherKey = 'a913b85241698a00b1014abe62a5ca0e';
        const locationKey = '74b2d16131a57f25ad6a62156047b305';
        const city = document.getElementById('search').value;

        await fetch(`https://api.positionstack.com/v1/forward?access_key=${locationKey}&query=${city}&limit=1`)
        .then((response) => {return response.json()})
        .then((data) => {
           this.setState({lon: data.data[0].longitude, lat: data.data[0].latitude, city: city});
        })    

        await fetch(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.state.lat}&lon=${this.state.lon}&exclude=hourly,minutely&appid=${weatherKey}&units=metric`)
        .then((response) => {return response.json()})
        .then((data) => {
            this.setState({data: data})
        })
    }

    componentDidMount() {
        const geo = navigator.geolocation;
        
        if(!geo) {
            this.fail()
        } else {
            geo.getCurrentPosition(this.success, this.fail)
        }
    }

    componentWillUnmount() {
    }

    render() {
        if (this.state.data == null) {
            return (
                <div className="loading">
                    <span>Loading data, please wait</span>
                </div>
            )
        } else {}
        return (
            <div className="app">
                <form className="find">
                    <input id="search" type="text" placeholder="Enter location name" pattern="[A-Za-z]"></input>
                    <button type="submit" onClick={this.findCity.bind(this)}>Search</button>
                </form>
                <CurrentDay weather={this.state.data.current} city={this.state.city}/>
                <div className="future">
                    <NextDays weather={this.state.data.daily} city={this.state.city} id={1}/>
                    <NextDays weather={this.state.data.daily} city={this.state.city} id={2}/>
                    <NextDays weather={this.state.data.daily} city={this.state.city} id={3}/>
                    <NextDays weather={this.state.data.daily} city={this.state.city} id={4}/>
                    <NextDays weather={this.state.data.daily} city={this.state.city} id={5}/>
                    <NextDays weather={this.state.data.daily} city={this.state.city} id={6}/>
                    <NextDays weather={this.state.data.daily} city={this.state.city} id={7}/>
                </div>
            </div>
        );
    }
}


//================================================================
ReactDOM.render(
    <App />,
    document.getElementById('root')
)